import React from "react";

const Navbar: React.FC = () => {
  return (
    <nav className="bg-white shadow-md w-11/12 mx-auto mt-4 rounded-[48px]">
      <div className="max-w-7xl mx-auto flex flex-wrap items-center justify-between py-4 px-6 gap-3">
        {/* Logo */}
        <div className="flex items-center flex-shrink-0">
          <h1 className="text-[28px] md:text-[32px] font-normal text-[#212121] leading-[20px]">
            DriveEasy
          </h1>
        </div>

        {/* Links: they will wrap to a new row on small screens */}
        <ul className="w-full md:w-auto flex flex-wrap md:flex-nowrap justify-center md:justify-start gap-3 md:gap-10 text-sm md:text-base">
          <li className="px-2 py-1 hover:text-gray-600 cursor-pointer">Home</li>
          <li className="px-2 py-1 hover:text-gray-600 cursor-pointer">
            Browse Cars
          </li>
          <li className="px-2 py-1 hover:text-gray-600 cursor-pointer">
            My Bookings
          </li>
          <li className="px-2 py-1 hover:text-gray-600 cursor-pointer">
            Contact Us
          </li>
          <li className="px-2 py-1 hover:text-gray-600 cursor-pointer">
            About Us
          </li>
        </ul>

        {/* Buttons */}
        <div className="flex items-center gap-3 flex-shrink-0">
          {/* hide small Login button to save space, keep sign-up visible */}
          <button className="hidden sm:inline border-2 border-[#212121] text-[#212121] py-2 px-4 rounded-[24px] text-sm">
            Login
          </button>
          <button className="bg-[#212121] text-white py-2 px-5 rounded-[24px] text-sm">
            Sign up
          </button>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
